/* header-search-integration.css - Ð¡Ñ‚Ð¸Ð»Ñ– Ð´Ð»Ñ Ñ–Ð½Ñ‚ÐµÐ³Ñ€Ð°Ñ†Ñ–Ñ— ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð¿Ð¾ÑˆÑƒÐºÑƒ Ð² header */

/* ÐŸÐ¾ÐºÑ€Ð°Ñ‰ÐµÐ½Ð° ÐºÐ½Ð¾Ð¿ÐºÐ° Ð¿Ð¾ÑˆÑƒÐºÑƒ Ð² header */
.enhanced-search-btn {
  display: flex !important;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  background: linear-gradient(135deg, #008080 0%, #006666 100%);
  border: none;
  border-radius: 25px;
  color: white;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 128, 128, 0.2);
  position: relative;
  overflow: hidden;
}

.enhanced-search-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.enhanced-search-btn:hover::before {
  left: 100%;
}

.enhanced-search-btn:hover {
  background: linear-gradient(135deg, #006666 0%, #004d4d 100%);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 128, 128, 0.3);
}

.enhanced-search-btn:active {
  transform: translateY(0);
}

.enhanced-search-btn .search-icon {
  width: 18px;
  height: 18px;
  transition: transform 0.3s ease;
}

.enhanced-search-btn:hover .search-icon {
  transform: scale(1.1) rotate(15deg);
}

.enhanced-search-btn .search-text {
  font-weight: 600;
}

/* ÐÐ´Ð°Ð¿Ñ‚Ð¸Ð²Ð½Ñ–ÑÑ‚ÑŒ Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
@media (max-width: 768px) {
  .enhanced-search-btn .search-text {
    display: none;
  }
  
  .enhanced-search-btn {
    padding: 12px;
    border-radius: 50%;
    min-width: 44px;
    min-height: 44px;
    justify-content: center;
  }
  
  .enhanced-search-btn .search-icon {
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 480px) {
  .enhanced-search-btn {
    padding: 10px;
    min-width: 40px;
    min-height: 40px;
  }
  
  .enhanced-search-btn .search-icon {
    width: 18px;
    height: 18px;
  }
}

/* ÐžÐ¿Ñ‚Ð¸Ð¼Ñ–Ð·Ð°Ñ†Ñ–Ñ Ð´Ð»Ñ Ñ–ÑÐ½ÑƒÑŽÑ‡Ð¸Ñ… ÑÑ‚Ð¸Ð»Ñ–Ð² header */
.nav-section {
  position: relative;
}

.desktop-nav {
  margin-right: 20px;
}

/* ÐŸÐ¾ÐºÑ€Ð°Ñ‰ÐµÐ½Ð¸Ð¹ z-index Ð´Ð»Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð° */
.search-modal-overlay {
  z-index: 9999 !important;
}

/* ÐŸÐ»Ð°Ð²Ð½Ð¸Ð¹ Ð¿ÐµÑ€ÐµÑ…Ñ–Ð´ Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
.search-button {
  transition: all 0.3s ease;
}

/* ÐŸÑ–Ð´Ñ‚Ñ€Ð¸Ð¼ÐºÐ° Ñ–ÑÐ½ÑƒÑŽÑ‡Ð¸Ñ… ÑÑ‚Ð¸Ð»Ñ–Ð² */
.header .search-button.enhanced-search-btn {
  margin-left: auto;
}

/* Ð¤Ð¾ÐºÑƒÑ ÑÑ‚Ð°Ð½ Ð´Ð»Ñ accessibility */
.enhanced-search-btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 128, 128, 0.3);
}

/* ÐÐ½Ñ–Ð¼Ð°Ñ†Ñ–Ñ Ð¿Ñ€Ð¸ Ð·Ð°Ð²Ð°Ð½Ñ‚Ð°Ð¶ÐµÐ½Ð½Ñ– */
@keyframes searchButtonFadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.enhanced-search-btn {
  animation: searchButtonFadeIn 0.5s ease 0.2s both;
}

/* ÐŸÐ¾ÐºÑ€Ð°Ñ‰ÐµÐ½Ð½Ñ Ð´Ð»Ñ Ñ‚ÐµÐ¼Ð½Ð¾Ñ— Ñ‚ÐµÐ¼Ð¸ */
@media (prefers-color-scheme: dark) {
  .enhanced-search-btn {
    background: linear-gradient(135deg, #008080 0%, #006666 100%);
    box-shadow: 0 2px 8px rgba(0, 128, 128, 0.4);
  }
  
