}

/* Ð¡Ñ‚Ð¸Ð»Ñ– Ð´Ð»Ñ Ð¿Ñ–Ð´ÐºÐ°Ð·Ð¾Ðº */
.enhanced-search-btn[title]:hover::after {
  content: attr(title);
  position: absolute;
  bottom: -35px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 0.8rem;
  white-space: nowrap;
  z-index: 1000;
  opacity: 0;
  animation: tooltipFadeIn 0.3s ease 0.5s forwards;
}

@keyframes tooltipFadeIn {
  to {
    opacity: 1;
  }
}

/* Ð†Ð½Ñ‚ÐµÐ³Ñ€Ð°Ñ†Ñ–Ñ Ð· Ñ–ÑÐ½ÑƒÑŽÑ‡Ð¾ÑŽ ÑÑ–Ñ‚ÐºÐ¾ÑŽ header */
