/* Enhanced Search Styles - src/styles/enhanced-search.css */

/* Ð—Ð¼Ñ–Ð½Ð½Ñ– Ð´Ð»Ñ Ð¿Ð¾ÑˆÑƒÐºÐ¾Ð²Ð¾Ñ— ÑÐ¸ÑÑ‚ÐµÐ¼Ð¸ */
:root {
  --search-primary: #008080;
  --search-primary-light: #e6f3f3;
  --search-accent: #FFA500;
  --search-accent-light: #fff4e6;
  --search-text: #333f4f;
  --search-text-light: #6c757d;
  --search-border: #dee2e6;
  --search-bg: #ffffff;
  --search-bg-light: #f8f9fa;
  --search-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  --search-shadow-light: 0 2px 10px rgba(0, 0, 0, 0.05);
  --search-radius: 12px;
  --search-radius-small: 8px;
}

/* ÐžÑÐ½Ð¾Ð²Ð½Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ðµ Ð²Ñ–ÐºÐ½Ð¾ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
.search-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  z-index: 2000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 50px 20px;
  animation: searchModalFadeIn 0.3s ease;
}

.search-modal-container {
  background: var(--search-bg);
  border-radius: 20px;
  width: 100%;
  max-width: 800px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
  animation: searchModalSlideIn 0.3s ease;
  overflow: hidden;
}

/* Ð—Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð° */
.search-modal-header {
  padding: 30px 30px 20px;
  border-bottom: 1px solid var(--search-border);
  background: linear-gradient(135deg, var(--search-bg) 0%, var(--search-bg-light) 100%);
}

.search-modal-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.search-modal-title h2 {
  margin: 0;
  color: var(--search-text);
  font-size: 1.8rem;
  font-weight: 600;
}

.search-modal-close {
  background: var(--search-bg-light);
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--search-text-light);
}

.search-modal-close:hover {
  background: var(--search-primary-light);
  color: var(--search-primary);
  transform: rotate(90deg);
}

/* ÐŸÐ¾Ð»Ðµ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
.search-input-container {
  position: relative;
  margin-bottom: 20px;
}

.search-modal-input {
  width: 100%;
  padding: 15px 50px 15px 20px;
  border: 2px solid var(--search-border);
  border-radius: var(--search-radius);
  font-size: 1.1rem;
  background: var(--search-bg);
  transition: all 0.3s ease;
  font-family: inherit;
}

.search-modal-input:focus {
  outline: none;
  border-color: var(--search-primary);
  box-shadow: 0 0 0 4px rgba(0, 128, 128, 0.1);
  background: var(--search-bg);
}

.search-clear-btn {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  background: var(--search-text-light);
  border: none;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.search-clear-btn:hover {
  background: var(--search-primary);
  transform: translateY(-50%) scale(1.1);
}

/* Ð’ÐºÐ»Ð°Ð´ÐºÐ¸ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
.search-tabs {
  display: flex;
  gap: 10px;
}

.search-tab {
  padding: 10px 20px;
  background: transparent;
  border: 2px solid var(--search-border);
  border-radius: var(--search-radius);
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--search-text-light);
}

.search-tab:hover {
  border-color: var(--search-primary-light);
  background: var(--search-primary-light);
  color: var(--search-primary);
}

.search-tab.active {
  background: var(--search-primary);
  border-color: var(--search-primary);
  color: white;
  box-shadow: var(--search-shadow-light);
}

/* ÐšÐ¾Ð½Ñ‚ÐµÐ½Ñ‚ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð° */
.search-modal-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px 30px;
}

.search-tab-content {
  height: 100%;
}

/* Ð¨Ð²Ð¸Ð´ÐºÐ¸Ð¹ Ð¿Ð¾ÑˆÑƒÐº - Ð¿Ð¾Ð¿ÑƒÐ»ÑÑ€Ð½Ñ– Ñ‚ÐµÐ³Ð¸ */
.search-suggestions {
  margin-bottom: 30px;
}

.search-suggestions h4 {
  margin: 0 0 15px 0;
  color: var(--search-text);
  font-size: 1.1rem;
  font-weight: 600;
}

.search-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.search-suggestion-tag {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  background: var(--search-bg-light);
  border: 1px solid var(--search-border);
  border-radius: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--search-text);
}

.search-suggestion-tag:hover {
  background: var(--search-primary-light);
  border-color: var(--search-primary);
  color: var(--search-primary);
  transform: translateY(-2px);
  box-shadow: var(--search-shadow-light);
}

.tag-icon {
  font-size: 1.1rem;
}

/* Ð ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ð¸ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
.search-results-list,
.global-search-results {
  margin-top: 20px;
}

.results-header {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--search-border);
}

.results-header h4 {
  margin: 0;
  color: var(--search-text);
  font-size: 1.1rem;
  font-weight: 600;
}

/* Ð ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ð¸ Ð¿Ð¾ÑˆÑƒÐºÑƒ (Ð¼Ñ–Ð½Ñ–Ð¼Ð°Ð»Ñ–ÑÑ‚Ð¸Ñ‡Ð½Ñ–) */
.search-result-item,
.global-result-item {
  display: flex;
  align-items: center;
  padding: 16px 20px;
  margin-bottom: 10px;
  background: var(--search-bg);
  border: 1px solid var(--search-border);
  border-radius: var(--search-radius);
  cursor: pointer;
  transition: all 0.3s ease;
  gap: 15px;
}

.search-result-item:hover,
.global-result-item:hover {
  background: var(--search-primary-light);
  border-color: var(--search-primary);
  transform: translateX(5px);
  box-shadow: var(--search-shadow-light);
}

.result-icon {
  width: 40px;
  height: 40px;
  background: var(--search-bg-light);
  border-radius: var(--search-radius-small);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  flex-shrink: 0;
}

/* ÐŸÑ€Ð¸Ñ…Ð¾Ð²ÑƒÑ”Ð¼Ð¾ Ð¿Ð¾Ñ€Ð¾Ð¶Ð½Ñ– Ñ–ÐºÐ¾Ð½ÐºÐ¸ */
.result-icon:empty {
  display: none;
}

/* ÐšÐ¾Ñ€Ð¸Ð³ÑƒÑ”Ð¼Ð¾ Ð²Ñ–Ð´ÑÑ‚ÑƒÐ¿Ð¸ ÐºÐ¾Ð»Ð¸ Ð½ÐµÐ¼Ð°Ñ” Ñ–ÐºÐ¾Ð½ÐºÐ¸ */
.result-icon:empty + .result-content {
  margin-left: 0;
}

.result-content {
  flex: 1;
}

.result-title {
  font-weight: 600;
  color: var(--search-text);
  margin-bottom: 5px;
  font-size: 1rem;
}

.result-description,
.result-text {
  color: var(--search-text-light);
  font-size: 0.9rem;
  line-height: 1.4;
  margin-bottom: 8px;
}

.result-meta {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 0.85rem;
}

.result-category,
.result-tag {
  color: var(--search-primary);
  font-weight: 500;
}

.result-relevance {
  color: var(--search-text-light);
  font-size: 0.8rem;
}

.result-arrow {
  color: var(--search-text-light);
  font-size: 1.2rem;
  transition: transform 0.3s ease;
}

.search-result-item:hover .result-arrow,
.global-result-item:hover .result-arrow {
  transform: translateX(5px);
  color: var(--search-primary);
}

/* ÐŸÑ–Ð´ÑÐ²Ñ–Ñ‡ÑƒÐ²Ð°Ð½Ð½Ñ Ñ‚ÐµÐºÑÑ‚Ñƒ */
.search-highlight-text {
  background: var(--search-accent-light);
  color: var(--search-accent);
  padding: 2px 4px;
  border-radius: 4px;
  font-weight: 600;
}

.search-highlight {
  animation: searchHighlight 2s ease;
  border: 2px solid var(--search-accent) !important;
  background: var(--search-accent-light) !important;
  border-radius: var(--search-radius-small);
}

/* Ð“Ð»Ð¾Ð±Ð°Ð»ÑŒÐ½Ð¸Ð¹ Ð¿Ð¾ÑˆÑƒÐº */
.global-search-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.global-search-filters {
  display: flex;
  gap: 20px;
  align-items: center;
  padding: 15px 20px;
  background: var(--search-bg-light);
  border-radius: var(--search-radius);
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.filter-group {
  display: flex;
  align-items: center;
  gap: 8px;
}

.filter-group label {
  font-size: 0.9rem;
  color: var(--search-text);
  font-weight: 500;
}

.filter-group select {
  padding: 6px 12px;
  border: 1px solid var(--search-border);
  border-radius: var(--search-radius-small);
  font-size: 0.9rem;
  background: var(--search-bg);
  color: var(--search-text);
}

.filter-group input[type="checkbox"] {
  margin-right: 5px;
}

.global-search-content {
  flex: 1;
  overflow-y: auto;
}

/* ÐšÐ°Ñ‚ÐµÐ³Ð¾Ñ€Ñ–Ñ— Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ñ–Ð² */
.result-category {
  margin-bottom: 25px;
}

.category-title {
  margin: 0 0 15px 0;
  color: var(--search-text);
  font-size: 1rem;
  font-weight: 600;
  padding: 10px 15px;
  background: var(--search-bg-light);
  border-radius: var(--search-radius-small);
  border-left: 4px solid var(--search-primary);
}

.category-results {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/* Ð¡Ñ‚Ð°Ð½Ð¸ Ð·Ð°Ð²Ð°Ð½Ñ‚Ð°Ð¶ÐµÐ½Ð½Ñ Ñ‚Ð° Ð¿Ð¾Ñ€Ð¾Ð¶Ð½Ñ–Ñ… Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ñ–Ð² */
.global-search-loading,
.search-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  color: var(--search-text-light);
}

.global-search-info {
  text-align: center;
  padding: 40px 20px;
  color: var(--search-text-light);
}

.global-search-info h4 {
  margin: 0 0 10px 0;
  color: var(--search-text);
}

.index-stats {
  margin-top: 15px;
  font-size: 0.85rem;
}

.global-search-empty,
.search-no-results {
  text-align: center;
  padding: 40px 20px;
  color: var(--search-text-light);
}

.global-search-empty h4,
.search-no-results h4 {
  margin: 0 0 10px 0;
  color: var(--search-text);
}

.btn-reindex {
  margin-top: 15px;
  padding: 10px 20px;
  background: var(--search-primary);
  color: white;
  border: none;
  border-radius: var(--search-radius-small);
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-reindex:hover {
  background: #006666;
  transform: translateY(-2px);
}

/* Ð†ÑÑ‚Ð¾Ñ€Ñ–Ñ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
.search-history {
  margin-top: 30px;
}

.search-section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--search-border);
}

.search-section-header h4 {
  margin: 0;
  color: var(--search-text);
  font-size: 1.1rem;
  font-weight: 600;
}

.clear-history-btn {
  background: none;
  border: none;
  color: var(--search-text-light);
  font-size: 0.9rem;
  cursor: pointer;
  transition: color 0.3s ease;
}

.clear-history-btn:hover {
  color: var(--search-primary);
}

.history-items {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.history-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background: var(--search-bg-light);
  border-radius: var(--search-radius-small);
  cursor: pointer;
  transition: all 0.3s ease;
}

.history-item:hover {
  background: var(--search-primary-light);
  transform: translateX(5px);
}

.history-content {
  flex: 1;
}

.history-query {
  font-weight: 600;
  color: var(--search-text);
  font-size: 0.95rem;
  display: block;
  margin-bottom: 3px;
}

.history-result {
  font-size: 0.85rem;
  color: var(--search-text-light);
}

.history-time {
  font-size: 0.8rem;
  color: var(--search-text-light);
}

/* Ð¨Ð²Ð¸Ð´ÐºÑ– Ð´Ñ–Ñ— */
.search-quick-actions {
  margin-top: 30px;
}

.search-quick-actions h4 {
  margin: 0 0 15px 0;
  color: var(--search-text);
  font-size: 1.1rem;
  font-weight: 600;
}

.quick-actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 15px;
}

.quick-action-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px 15px;
  background: var(--search-bg);
  border: 1px solid var(--search-border);
  border-radius: var(--search-radius);
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.quick-action-btn:hover {
  background: var(--search-primary-light);
  border-color: var(--search-primary);
  transform: translateY(-3px);
  box-shadow: var(--search-shadow-light);
}

.action-icon {
  font-size: 2rem;
}

.action-text {
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--search-text);
}

/* ÐŸÑ–Ð´ÐºÐ°Ð·ÐºÐ¸ Ð´Ð»Ñ Ð¿ÐµÑ€ÐµÑ…Ð¾Ð´Ñƒ */
.global-search-hint {
  margin-top: 20px;
  padding: 15px;
  background: linear-gradient(135deg, var(--search-primary-light) 0%, var(--search-accent-light) 100%);
  border-radius: var(--search-radius-small);
  border-left: 4px solid var(--search-primary);
}

.global-search-hint p {
  margin: 0;
  font-size: 0.9rem;
  color: var(--search-text);
}

/* Ð ÐµÐºÐ¾Ð¼ÐµÐ½Ð´Ð°Ñ†Ñ–Ñ— Ð¿Ñ€Ð¸ Ð²Ñ–Ð´ÑÑƒÑ‚Ð½Ð¾ÑÑ‚Ñ– Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ñ–Ð² */
.no-results-suggestions {
  margin-top: 20px;
}

.no-results-suggestions p {
  margin: 0 0 10px 0;
  font-size: 0.9rem;
  color: var(--search-text);
}

.suggestion-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
}

.suggestion-chip {
  padding: 6px 12px;
  background: var(--search-primary-light);
  border: 1px solid var(--search-primary);
  border-radius: 15px;
  font-size: 0.85rem;
  color: var(--search-primary);
  cursor: pointer;
  transition: all 0.3s ease;
}

.suggestion-chip:hover {
  background: var(--search-primary);
  color: white;
  transform: translateY(-2px);
}

/* Ð¡Ð¿Ñ–Ð½ÐµÑ€ Ð·Ð°Ð²Ð°Ð½Ñ‚Ð°Ð¶ÐµÐ½Ð½Ñ */
.search-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid var(--search-border);
  border-top-color: var(--search-primary);
  border-radius: 50%;
  animation: searchSpin 1s linear infinite;
  margin-bottom: 15px;
}

/* ÐŸÑ–Ð´Ð²Ð°Ð» Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð° */
.search-modal-footer {
  padding: 15px 30px;
  border-top: 1px solid var(--search-border);
  background: var(--search-bg-light);
}

.search-tips {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.search-tip {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 0.85rem;
  color: var(--search-text-light);
}

.search-tip kbd {
  background: var(--search-bg);
  border: 1px solid var(--search-border);
  border-radius: 4px;
  padding: 2px 6px;
  font-size: 0.8rem;
  font-family: monospace;
}

/* ÐÐ½Ñ–Ð¼Ð°Ñ†Ñ–Ñ— */
@keyframes searchModalFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes searchModalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes searchSpin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes searchHighlight {
  0% {
    background: var(--search-accent);
    transform: scale(1.05);
  }
  50% {
    background: var(--search-accent-light);
    transform: scale(1.02);
  }
  100% {
    background: transparent;
    transform: scale(1);
  }
}

/* ÐÐ´Ð°Ð¿Ñ‚Ð¸Ð²Ð½Ð¸Ð¹ Ð´Ð¸Ð·Ð°Ð¹Ð½ */
@media (max-width: 768px) {
  .search-modal-overlay {
    padding: 20px 15px;
    align-items: flex-start;
  }

  .search-modal-container {
    max-height: 95vh;
    border-radius: 15px;
  }

  .search-modal-header {
    padding: 20px 20px 15px;
  }

  .search-modal-title h2 {
    font-size: 1.5rem;
  }

  .search-modal-content {
    padding: 15px 20px;
  }

  .search-modal-input {
    font-size: 1rem;
    padding: 12px 40px 12px 15px;
  }

  .search-tabs {
    flex-direction: column;
  }

  .search-tab {
    text-align: center;
  }

  .search-tags {
    justify-content: center;
  }

  .global-search-filters {
    flex-direction: column;
    align-items: stretch;
    gap: 15px;
  }

  .filter-group {
    justify-content: space-between;
  }

  .quick-actions-grid {
    grid-template-columns: 1fr;
  }

  .search-tips {
    justify-content: center;
    gap: 15px;
  }

  .search-result-item,
  .global-result-item {
    padding: 12px 15px;
  }

  .result-icon {
    width: 35px;
    height: 35px;
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .search-modal-overlay {
    padding: 10px;
  }

  .search-modal-header {
    padding: 15px;
  }

  .search-modal-content {
    padding: 10px 15px;
  }

  .search-modal-footer {
    padding: 10px 15px;
  }

  .search-tips {
    flex-direction: column;
    gap: 10px;
  }
}

/* Ð¡Ñ‚Ð¸Ð»Ñ– Ð´Ð»Ñ Ð³Ñ–Ð±Ñ€Ð¸Ð´Ð½Ð¾Ð³Ð¾ Ð¿Ð¾ÑˆÑƒÐºÑƒ */
.dynamic-result {
  border-left: 3px solid var(--search-accent);
}

.static-result {
  border-left: 3px solid var(--search-primary);
}

.dynamic-badge {
  display: inline-block;
  padding: 2px 6px;
  background: var(--search-accent);
  color: white;
  font-size: 0.7rem;
  border-radius: 10px;
  margin-left: 8px;
  font-weight: 600;
}

.btn-stats {
  padding: 6px 12px;
  background: var(--search-bg-light);
  border: 1px solid var(--search-border);
  border-radius: var(--search-radius-small);
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--search-text);
}

.btn-stats:hover {
  background: var(--search-primary-light);
  border-color: var(--search-primary);
}

/* ÐŸÐ¾ÐºÑ€Ð°Ñ‰ÐµÐ½Ð° ÑÑ‚Ð°Ñ‚Ð¸ÑÑ‚Ð¸ÐºÐ° */
.index-stats {
  margin-top: 15px;
  padding: 10px;
  background: var(--search-bg-light);
  border-radius: var(--search-radius-small);
  font-size: 0.85rem;
  line-height: 1.4;
}

.index-stats strong {
  color: var(--search-primary);
}

/* ÐŸÐ¾ÐºÑ€Ð°Ñ‰ÐµÐ½Ð½Ñ Ð´Ð»Ñ Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ñ–Ð² */
.results-header {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--search-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.results-header small {
  color: var(--search-text-light);
  font-size: 0.8rem;
}
@media (prefers-color-scheme: dark) {
  :root {
    --search-bg: #1a1a1a;
    --search-bg-light: #2d2d2d;
    --search-text: #ffffff;
    --search-text-light: #b0b0b0;
    --search-border: #404040;
