}

.order-modal-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #333f4f;
  margin: 0 40px 0 0;
  font-family: 'Montserrat', sans-serif;
}

.order-modal-subtitle {
  font-size: 0.95rem;
