  margin: 8px 0 0 0;
  line-height: 1.4;
}

/* ÐšÐ½Ð¾Ð¿ÐºÐ° Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ */
.order-close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 36px;
  height: 36px;
  background: transparent;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6c757d;
  transition: all 0.3s ease;
}

.order-close-btn:hover {
  background: #f8f9fa;
  color: #333f4f;
  transform: rotate(90deg);
}

.order-close-btn svg {
  width: 18px;
  height: 18px;
}

/* Ð¢Ñ–Ð»Ð¾ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð° */
.order-modal-body {
  max-height: 65vh;
  overflow-y: auto;
  padding: 24px;
}

/* Ð¡Ñ‚Ð¸Ð»Ñ– Ð´Ð»Ñ Ñ„Ð¾Ñ€Ð¼Ð¸ */
.order-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-label {
