  color: #333f4f;
  margin-bottom: 8px;
  font-size: 0.95rem;
}

.form-label.required::after {
  content: ' *';
  color: #dc3545;
}

.form-input,
.form-select,
.form-textarea {
  padding: 12px 16px;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
  font-family: inherit;
  outline: none;
  transition: all 0.3s ease;
  background: #f8f9fa;
}

.form-input:focus,
.form-select:focus,
.form-textarea:focus {
  border-color: #008080;
  background: #ffffff;
  box-shadow: 0 0 0 3px rgba(0, 128, 128, 0.1);
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
  font-family: inherit;
}

/* Radio buttons Ð´Ð»Ñ Ñ‚Ð¸Ð¿Ñƒ Ð´Ð¾ÑÑ‚Ð°Ð²ÐºÐ¸ */
.radio-group {
  display: flex;
  gap: 20px;
  margin-top: 8px;
}

.radio-option {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.radio-option:hover {
  background-color: #f8f9fa;
}

.radio-option input[type="radio"] {
  width: 18px;
  height: 18px;
  margin: 0;
  accent-color: #008080;
}

.radio-option label {
  margin: 0;
  cursor: pointer;
  font-weight: 500;
  color: #333f4f;
}

/* Ð£Ð¼Ð¾Ð²Ð½Ñ– Ð¿Ð¾Ð»Ñ */
.conditional-field {
