  background: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #008080;
}

/* ÐšÐ½Ð¾Ð¿ÐºÐ¸ Ñ„Ð¾Ñ€Ð¼Ð¸ */
.form-actions {
  display: flex;
  gap: 12px;
  margin-top: 24px;
  flex-wrap: wrap;
}

.btn-order-submit {
