  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: inherit;
}

.btn-order-submit:hover {
  background: #006666;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 128, 128, 0.3);
}

.btn-order-submit:disabled {
