/* Ð¡Ñ‚Ð¸Ð»Ñ– Ð´Ð»Ñ ÐºÐ»Ñ–ÐºÐ°Ð±ÐµÐ»ÑŒÐ½Ð¾Ð³Ð¾ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ñƒ */
.phone-link {
  color: #008080;
  text-decoration: none;
  font-weight: 600;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease;
  padding: 2px 6px;
  border-radius: 4px;
  white-space: nowrap;
}

.phone-link:hover {
  color: #006666;
  background-color: #e6f3f3;
  border-bottom-color: #008080;
  text-decoration: none;
  transform: scale(1.02);
}

.phone-link:active {
  transform: scale(0.98);
}

/* ÐÐ½Ñ–Ð¼Ð°Ñ†Ñ–Ñ— */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideDown {
  from { 
    opacity: 0; 
    transform: translateY(-20px) scale(0.95); 
  }
  to { 
    opacity: 1; 
    transform: translateY(0) scale(1); 
  }
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* ÐšÐ°ÑÑ‚Ð¾Ð¼Ð½Ð° Ð¿Ð¾Ð»Ð¾ÑÐ° Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ¸ Ð´Ð»Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð° */
.order-modal-body {
  scrollbar-width: auto;
